<template lang="html">
  <div class="rank" ref="rank">
    <scroller :data="topList" class="top-list" ref="scroller">
      <div class="loading-wrapper" v-show="topList.length == 0">
        <loading :scale="1.5"></loading>
      </div>
      <ul>
        <li @click="selectItem(item)" class="item" v-for="item in topList">
          <div class="icon">
            <img v-lazy="item.picUrl" width="100" height="100">
          </div>
          <ul class="song-list">
            <li class="song" v-for="(song, index) in item.songList">
              <span>{{ index + 1 }}</span>
              <span>{{ song.songname }} - {{ song.singername }}</span>
            </li>
          </ul>
        </li>
      </ul>
    </scroller>
    <router-view></router-view>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'
  import * as atypes from '@/store/action-types'
  import Scroller from 'components/base/scroller/scroller'
  import Loading from 'components/base/loading/loading3'
  import PlayerMixin from '@/mixins/player-mixin'
  import { miniPlayerHeight } from 'assets/js/config'

  export default {
    name: 'rank',
    components: { Scroller, Loading },
    mixins: [PlayerMixin],
    computed: {
      ...mapGetters([
        'topList'
      ])
    },
    created () {
      this.getTopList()
    },
    methods: {
      ...mapActions({
        getTopList: atypes.GET_TOP_LIST
      }),
      selectItem (item) {
        this.$router.push({
          path: `/rank/${item.id}`
        })
      },
      handlePlaylist (playlist) {
        const bottom = playlist.length > 0 ? miniPlayerHeight : 0
        this.$refs.rank.style.bottom = bottom
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .rank {
    position: absolute;
    width: 100%;
    top: 88px;
    bottom: 0;
    overflow: hidden;
    .top-list {
      height: 100%;
      overflow: hidden;
      .item {
        display: flex;
        margin: 0 20px;
        padding-bottom: 20px;
        height: 100px;
        .icon {
          flex: 0 0 100px;
          width: 100px;
          height: 100px;
        }
        .song-list {
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding: 0 20px;
          height: 100px;
          overflow: hidden;
          background: @color-highlight-background;
          color: @color-text-d;
          font-size: @font-size-small;
          .song {
            .no-wrap();
            line-height: 26px;
          }
        }
      }
    }
  }

  .loading-wrapper {
    margin-top: 20px;
    text-align: center;
  }
</style>



